import React, { FC } from 'react'
import clsx from 'clsx'
import { HeartWithBorderIcon } from '../../icons/HeartWithBorder'
import { HeartButtonProps } from './types'
import './HeartButton.scss'

const HeartButton: FC<HeartButtonProps> = ({
  className,
  isActive,
  onClick,
  size = 'M',
}) => {
  return (
    <button
      className={clsx(
        'HeartButton',
        isActive && 'HeartButton_Liked',
        className,
      )}
      onClick={onClick}
    >
      <HeartWithBorderIcon size={size} />
    </button>
  )
}

export default HeartButton
